import React from "react"

import Layout from "../components/layout"
import Image from "../components/image"
import SEO from "../components/seo"
import { Link } from "gatsby"

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    <div className="paragraph paragraph--small">
      <h1>Hi!</h1>
      <p>It seems you have found your way to my corner on the Internet.</p>
      <p>I'm honored by your presence here.</p>
      <p>My ambition is to deliver unique experiences through my own style of web design.</p>
      <p>
          Interested to work with me?
          <Link to="/contact/" className="button button--inline">Let's start a conversation!</Link>
      </p>
    </div>
    <Image modifier="home" />
  </Layout>
)

export default IndexPage
